.widgetLg{
     flex: 2;
     box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -webkit-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -moz-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     padding: 20px;
     margin-right: 20px;
}
.widgetLgTittle{
     font-size: 22px;
     font-weight: 600;
}
.widgetLgTable{
     width: 100%;
     border-spacing: 20px;
}
.widgetLargeTr{
     text-align: left;
}

.widgetLgUser{
     display: flex;
     align-items: center;
     font-weight: 600;
}
.widgetLgImg{
     height: 40px;
     width: 40px;
     border-radius: 50%;
     object-fit: cover;
     margin-right: 10px;
}

.widgetLgDate,.widgetLgAmount{
     font-weight: 300;
}

.widgetLgButton{
     padding: 5px 7px;
     border: none;
     border-radius: 10px;
}
.widgetLgButton.Approved{
     background-color: #e5faf2;
     color: #3bb077;
     
}
.widgetLgButton.Pending{
     background-color: #ebf1fe;
     color: #2a7ada;
}
.widgetLgButton.Declined{
     background-color: #fff0f1;
     color: #d95087;

}
