.featured{
     width: 100%;
     display: flex;
     justify-content:space-between;
}
.featuredItem{
     flex: 1;
     margin: 0 20px;
     padding: 30px;
     border-radius: 10px;
     cursor: pointer;
     box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
-webkit-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
-moz-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
}
.featuredTittle{
     font-size: 20px;
}
.featuredMoneyContainer{
     margin-top: 10px 0;
     display: flex;
     align-items: center;
}
.featuredMoney{
     font-size: 30px;
     font-weight: 400;
}
.featuredMoneyRate{
     display: flex;
     align-items: center;
     margin-left: 20px;
}
.featuredIcon{
     font-size: 14px;margin-left: 5px;
     color: green;
}
.featuredIcon.negative{
     color: red;
}
.featuredSub{
     font-size: 15px;
     color: gray;
}