.user{
     flex: 4;
     padding: 20px;
}
.userTittleContainer{
display: flex;
align-items: center;
justify-content: space-between;
margin-right: 5px;
}
.userAddbtn{
     width: 80px;
     border: none;
     padding: 5px;
     background-color: teal;
     border-radius: 5px;
     cursor: pointer;  
     color: white;
     font-size: 16px;  
      
}
.userContainer{
     display: flex;
     margin: 20px;
}
.userShow{
     flex: 1;
     padding: 20px;
     box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -webkit-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -moz-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
}
.userShowTop{
     display: flex;
     align-items: center;
}
.userShowImg{
     height: 40px;
     width: 40px;
     border-radius: 50%;
     object-fit: cover;
}

.userShowTopTittle{
     display: flex;
     flex-direction: column;
     margin-left: 20px;

}
.userShowUsername{
     font-weight: 600;
}
.userShowUserTittle{
     font-weight: 300;
}

.userShowBottom{
     margin-top: 20px;
}
.userShowTittle{
     font-size: 14px;
     font-weight: 600;
     color: rgb(184, 181, 181);
}
.userShowInfo{
     display: flex;
     align-items: center;
     margin: 20px 0px;
     color: #444;

}
.userShowIcon{
     font-size: 18px !important;

}
.userShowInfoTittle{
     margin-left: 10px;

}

.userUpdate{
     flex: 2;
     padding: 20px;
     box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -webkit-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -moz-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     margin-left: 20px;
}

.userUpdateTittle{
     font-size: 24px;
     font-weight: 600;
}
.userUpdateForm{
     display: flex;
     justify-content: space-between;
      margin-top: 20px;
}
.userUpdateItem{
     display: flex;
     flex-direction: column;
     margin-top: 10px;
}
.userUpdateItem>label{
margin-bottom: 5px;
font-size: 14px;

}
.userUpdateInput{
     border: none;
     height: 30px;
     width: 250px;
     border-bottom: 1px solid rgb(176, 172, 172);
}
.userUpdateRight{
     display: flex;
     flex-direction: column;
     justify-content: space-between;
}
.userUpdateUpload{
     display: flex;
     align-items: center;
     cursor: pointer;
}

.userUpdateImg{
     width: 100px;
     height: 100px;
     border-radius: 10px;
     object-fit: cover;
     margin-right: 20px;
}
.userUpdateIcon{
cursor: pointer;
}
.userUpdateBtn{
     border-radius: 5px;
     border: none;
     padding: 5px;
     cursor: pointer;
     background-color: darkcyan;
     color: white;
     font-weight: 600;
}