.userList{
     flex: 4;
}
.userListUser{
     display: flex;
     align-items: center;
}
.userListImg{
     height: 32px;
     width: 32px;
     border-radius: 50%;
     object-fit: cover;
margin-right: 10px;
}
.userListEdit{
     border: none;
     border-radius: 10px;
     padding: 5px 10px;
     background-color: #3bb;
     color: white;
     cursor: pointer;
     margin-right: 20px;
}
.userListDelete{
     color: rgb(236, 139, 139);
     cursor: pointer;
}