.widgetSm{
     flex: 1;
     box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -webkit-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     -moz-box-shadow: 3px 0px 8px 2px rgba(230,222,222,0.64);
     padding: 20px;
     margin-right: 20px;
}
.widgetSmTittle{
     font-size: 22px;
     font-weight: 600;
}
.widgetSmList{
margin: 0;
padding: 0;
list-style: none;
}
.widgetSmImg{
     height: 40px;
     width: 40px;
     border-radius: 50%;
     object-fit: cover;
}
.widgetSmListItem{
     display: flex;
     align-items: center;
     justify-content: space-between;
     margin: 20px 0;
}
.widgetSmUser{
     display: flex;
     flex-direction: column;
}
.widgetUserName{
     font-weight: 600;
}
.widgetUserTittle{
     font-weight: 300;
}
.widgetSmButton{
     display: flex;
     align-items: center;
     border: none;
     border-radius: 10px;
     padding: 7px 10px;
     background-color: #eeeef7;
     color: #555;
     cursor: pointer;
}
.widgetSmIcon{
     margin-right: 5px;
     font-size: 17px !important;
}