.newUser{
     flex:4
}
.newUserForm{
     display: flex;
     flex-wrap: wrap;
}
.newUserItem{
     width: 400px;
     display: flex;
     flex-direction: column;
     margin-top: 10px;margin-right: 20px;
}
.newUserItem>label{
     margin-bottom: 10px;
     font-size: 14px;font-weight: 600;
     color: gray;
}
.newUserItem>input{
     height: 20px;
     padding: 10px;
     border: 1px solid gray;
     border-radius: 8px;
     margin-left: 2px;
}
.newUserGender>input{
     margin-top: 15px;
}
.newUserGender>label{
     margin: 10px;
     font-size: 18px;
     color: #555;
}
.newUserSelect{
     height: 30px;
     /* width: 70px; */
     border-radius: 5px;
     background-color: none;
     
}
.newuserBtn{
     width: 100px;
     border: none;
     background-color: cadetblue;
     color: white;
     font-weight: 600;
     padding: 7px 10px;
     border-radius: 10px;
     margin-top: 30px;
     margin-left: 10px;
     cursor: pointer;
}